@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.inner{max-width:60%;margin-left:auto;margin-right:auto}.section-head{color:#fff;padding-left:5px;margin-bottom:15px;font-size:1.3em;font-family:"Roboto", sans-serif}.playlists{display:grid;grid-template-columns:repeat(6, auto);justify-items:center;text-align:center}.playlist{margin-bottom:20px}.playlist img{width:200px;height:200px}.playlist h3{color:beige;font-size:1em;width:90%;margin-left:auto;margin-right:auto}.playlist p{color:#fff}body{font-family:"Lato", sans-serif;background:#191414}

header{padding-top:20px;color:#fff}header img{max-width:100px;margin-right:10px}header h1{font-size:2em}header a{text-decoration:none;color:#fff;padding-right:10px}#header-container{display:flex;justify-content:space-between}#header-left{display:flex}#header-left h1{margin:0;font-size:1.5em}



